<template>
    <div class="page-wrap">
        <div class="title-container">
            <h1 class="ranking-title">&lt; 신마법의 대륙 랭킹 &gt;</h1>
            <span class="current-date"> 조회시간 : {{ currentDate }}</span>
        </div>
        <table v-if="rankData.length">
            <thead>
                <tr>
                    <th v-for="header in tableHeaders" :key="header">
                        {{ header }}
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in rankData" :key="item.rank">
                    <td>{{ item.rank }}</td>
                    <td>{{ item.name }}</td>
                    <td class="class-container">
                        <img
                            :src="getClassImageUrl(item.class)"
                            :alt="item.class"
                            class="class-icon"
                        />
                        <span>{{ item.class }}</span>
                    </td>
                    <td>{{ formatNumber(calculateRebirth(item.remotal)) }}</td>
                    <td>{{ formatNumber(item.remotal) }}</td>
                    <td>{{ formatNumber(item.level) }}</td>
                    <td>{{ item.tribe }}</td>
                </tr>
            </tbody>
        </table>

        <div class="pagination">
            <button
                @click="changePage(currentPage - 1)"
                :disabled="currentPage === 1"
            >
                이전
            </button>
            <button
                v-for="page in displayedPages"
                :key="page"
                @click="changePage(page)"
                :class="{ active: currentPage === page }"
            >
                {{ page }}
            </button>
            <button
                @click="changePage(currentPage + 1)"
                :disabled="currentPage === totalPages"
            >
                다음
            </button>
        </div>
    </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from "vue";
import { getRanking } from "@/api";
import dayjs from "dayjs";

const currentDate = ref("");
const rankData = ref([]);
const tableHeaders = [
    "순위",
    "캐릭터",
    "직업",
    "환생",
    "리모",
    "리모탈",
    "종족",
];
const currentPage = ref(1);
const totalPages = ref(10); // 예시로 10페이지로 설정, API 응답에 따라 조정 가능

const displayedPages = computed(() => {
    const start = Math.max(1, currentPage.value - 2);
    const end = Math.min(totalPages.value, start + 4);
    return Array.from({ length: end - start + 1 }, (_, i) => start + i);
});

function formatNumber(num) {
    return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

function calculateRebirth(remotal) {
    if (remotal <= 250) {
        return 0;
    }
    return Math.floor((remotal - 1) / 250);
}

function parseRankingData(htmlString) {
    const parser = new DOMParser();
    const doc = parser.parseFromString(htmlString, "text/html");
    const rows = doc.querySelectorAll("tr.grid_class_rows");

    return Array.from(rows).map((row) => {
        const cells = row.querySelectorAll("td");
        return {
            rank: parseInt(cells[0].textContent, 10),
            name: cells[1].textContent,
            remotal: parseInt(cells[2].textContent, 10),
            level: parseInt(cells[3].textContent, 10),
            tribe: cells[4].textContent,
            class: cells[5].textContent,
        };
    });
}

const changePage = (newPage) => {
    if (newPage >= 1 && newPage <= totalPages.value) {
        currentPage.value = newPage;
    }
};

watch(currentPage, (newPage) => {
    fetchRanking(newPage);
});

onMounted(() => {
    fetchRanking(currentPage.value);
});

const fetchRanking = async (page) => {
    try {
        const res = await getRanking(page);
        rankData.value = parseRankingData(res);
        currentDate.value = dayjs().format("YYYY-MM-DD HH:mm:ss");
        // 여기에서 총 페이지 수를 설정할 수 있습니다 (API가 이 정보를 제공한다면)
        // totalPages.value = ...
    } catch (err) {
        console.error("Failed to fetch ranking data:", err);
    }
};

function getClassImageUrl(className) {
    const filename = convertClassToFilename(className);
    return `/images/${filename}`; // public 폴더에 있다고 가정
}
const convertClassToFilename = (className) => {
    switch (className) {
        case "도둑":
            return "thief.png";
        case "전사":
            return "warrior.png";
        case "소환":
            return "summoner.png";
        case "마법":
            return "mage.png";
        case "성직":
            return "cleric.png";
        case "음유":
            return "bard.png";
        default:
            console.warn(`Unknown class name: ${className}`);
            return "unknown.png";
    }
};
</script>

<style scoped>
.page-wrap {
    padding: 8px;
}
.title-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 8px;
    margin-bottom: 12px;
}

.ranking-title {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    margin-right: 15px;
}

.current-date {
    font-size: 14px;
    color: #666;
}

table {
    width: 100%;
    border-collapse: collapse;
}
tr {
    height: 36px;
}
th,
td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: center;
    vertical-align: middle; /* 세로 중앙 정렬 추가 */
}
th {
    background-color: #979797; /* 헤더 배경색을 더 진하게 변경 */
    font-weight: bold; /* 헤더 텍스트를 굵게 */
}
/* 짝수 행 배경색 설정 */
tbody tr:nth-child(even) {
    background-color: #ebebeb; /* 아주 연한 회색 */
}
/* 각 열의 상대적 너비 설정 */
th:nth-child(1),
td:nth-child(1) {
    width: 8%;
} /* 순위 */
th:nth-child(2),
td:nth-child(2) {
    width: 20%;
} /* 캐릭터 */
th:nth-child(3),
td:nth-child(3) {
    width: 15%;
} /* 직업 */
th:nth-child(4),
td:nth-child(4) {
    width: 12%;
} /* 환생 */
th:nth-child(5),
td:nth-child(5) {
    width: 12%;
} /* 리모 */
th:nth-child(6),
td:nth-child(6) {
    width: 15%;
} /* 리모탈 */
th:nth-child(7),
td:nth-child(7) {
    width: 18%;
} /* 종족 */

.pagination {
    margin-top: 20px;
    text-align: center;
}
.pagination button {
    margin: 0 5px;
    padding: 5px 10px;
    cursor: pointer;
}
.pagination button.active {
    background-color: #4caf50;
    color: white;
}
.class-icon {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    vertical-align: middle; /* 아이콘 세로 중앙 정렬 */
}
</style>
